import React from 'react';
import { Link, Route, Routes } from 'react-router-dom';
import { ThemeProvider } from 'styled-components';
import { StandardCalculator } from './components/StandardCalculator';
import  ScientificCalculator  from './components/ScientificCalculator';
import { DateCalculator } from './components/DateCalculator';
import { CurrencyConverter } from './components/CurrencyConverter';
import  VolumeConverter  from './components/VolumeConverter';
import  LengthConverter  from './components/LengthConverter';
import { WeightConverter } from './components/WeightConverter';
import  TemperatureConverter  from './components/TemperatureConverter';
import { theme, GlobalStyle } from './theme';
import styled from 'styled-components'; 


const NavLink = styled(Link)`
  color: ${props => props.theme.colors.primary};
  text-decoration: none;
  padding: 10px;
  &:hover {
    background-color: ${props => props.theme.colors.secondary};
    border-radius: ${props => props.theme.borderRadius};
  }
`;

const Nav = styled.nav`
  display: flex;
  justify-content: center;
  flex-flow:row wrap;
  margin-bottom: 20px;
`;


function App() {
  return (
    <ThemeProvider theme={theme}>
      <GlobalStyle />
      <nav className="calculator-app">
        <Nav>
          <NavLink to="/"></NavLink>
          <NavLink to="/scientific"></NavLink>
          <NavLink to="/date"> </NavLink>
          <NavLink to="/currency"> </NavLink>
          <NavLink to="/volume"></NavLink>
          <NavLink to="/length"></NavLink>
          <NavLink to="/weight">  </NavLink>
          <NavLink to="/temperature"></NavLink>
        </Nav>
      </nav>
      <Routes>
        <Route exact path="/" element={<StandardCalculator />} />
        <Route path="/scientific" element={<ScientificCalculator />} />
        <Route path="/date" element={<DateCalculator />} />
        <Route path="/currency" element={<CurrencyConverter />} />
        <Route path="/volume" element={<VolumeConverter />} />
        <Route path="/length" element={<LengthConverter />} />
        <Route path="/weight" element={<WeightConverter />} />
        <Route path="/temperature" element={<TemperatureConverter />} />
      </Routes>
    </ThemeProvider>
  );
}

export default App;
